.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.fork {
  position: relative;
  padding-left: 1.5rem;
  float: right;
}
.fork:hover {
  background: #0ca58f;
}
.fork::before {
  content: '';
  position: absolute;
  width: 20px;
  left: 3px;
  top: 0;
  height: 20px;
  background-size: 20px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url(data:image/svg+xml;utf8;base64,)
}

.add-step {
  position: relative;
  padding-left: 1.5rem;
  /* float: right; */
}
.add-step:hover {
  background: #0ca58f;
}
.add-step::before {
  content: '';
  position: absolute;
  width: 18px;
  left: 3px;
  top: 0;
  height: 18px;
  background-size: 18px;
  top: 50%;
  transform: translateY(-50%);
  background-image:  url(data:image/svg+xml;utf8;base64,);
}

.the-code code {
  padding: .125rem .0rem .0625rem;
  font-size: 16px;
}

.the-code .the-operation .accordion-content{
  background: #271c2e;
}

.the-code .the-operation .one-hop .accordion-content .inputs{
  position: relative;
}
.the-code .the-operation .one-hop .accordion-content .inputs select{
  width: 100px;
  max-width: 100%;
  padding-right: 4px;
}

.set-of-ops > li {
  font-size: 16px;
}

.accordion-item.is-active > div {
  display: block;
}

.accordion-item.disabled {
  background: lightgray;
}
.accordion-item.disabled a{
  cursor: not-allowed;
  color: gray;
}


.static .accordion-title{
  background: #e6e6e6;
  margin-bottom: 0;
  font-size: 20px;
}
.static .accordion-title::before{
  content: "";
}
.static .accordion-content{
  display: block;
}


/*  ONE HOP  */
.one-hop{
  position: relative;
}

.inputs form select{
  background: #732c98;
  color: white;
}

.one-hop .inputs form .edge select{
  background: #219d8f;
  color: white;
}

.one-hop .inputs form.edgeR3 input{
  width: 90px;
  max-width: 100%;
  border: 3px solid dodgerblue;
  background: #219d8f;
  color: white;
}

.result-code {
  position: fixed;
  width: 100%;
  top: 50%;
  left: 0;
  transform: translate(0%, -50%);
}

.result-code::before {
  content: '';
  background: #000000c9;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -1;
  top: 0;
  transform: translateY(-22%);
}

/* Subgraph */
.subgraph span {
  color: white;
}


.inputs form select.attribute {
  background: white;
  color: black;
}

.logic, .inputs form select.logic {
  background: #8c8c8c;
}
